.customer-list {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
  align-items: center;
}

.customer-list__title-body {
  display: flex;
  justify-content: center;
  background-color: var(--white-blue-light);
  color: var(--blue-dark);
  width: 100%;
}

.customer-list__title {
  padding: .3em 0;
  width: 100%;
  max-width: 500px;
  font-size: 45pt;
}

.customer-list__main-body {
  width: 100%;
  max-width: 500px;
  margin-top: 3em;
}

.customer-list__main-title-body {
  padding: .7em 0;
  border-bottom: 2px solid var(--gray-transparent);
}

.customer-list__main-title {
  font-size: 12pt;
  color: var(--blue);
}

.customer-list__main-search-bar {
  display: flex;
  flex-flow: column nowrap;
  margin-top: 1.3em;
}

.customer-list__main-search-bar-label {
  color: var(--blue-dark);
  padding-bottom: .2em;
}

.customer-list__search-bar-input {
  outline: none;
  background-color: var(--white-blue-light);
  box-shadow: 0px 2px 2px var(--gray-transparent);
  border: none;
  color: var(--gray-blue-light);
  padding: 1em .8em;
  border-radius: 4px;
  font-size: 11pt;
  border: 2px solid transparent;
  transition: border .2s ease-in-out, box-shadow .2s ease-in-out;
}

.customer-list__search-bar-input:focus {
  border: 2px solid var(--blue);
  box-shadow: 0px 2px 2px var(--blue);
}